<template>
    <div class="main">
        <div v-wechat-title="$route.meta.title"></div>
        <!-- 代理注册 -->
        <div class="back" @click="goBack()">
            <img src="./img/left.png" alt="">
            <span>返回</span>
        </div>
        <div :class="topShow?'userInfo topShow1':'userInfo'">
            <div class="userName">
                <img src="./img/username.png" alt="">
                <input type="text" placeholder="请输入姓名" v-model="userName">
            </div>
            <div class="userPhone">
                <img src="./img/phone.png" alt="">
                <input type="number" @change="userPhoneTest(userPhone)" placeholder="请输入手机号" v-model="userPhone">
                <button class="verificationBtn" @click="yzm" v-if="isVerify">{{yzmContent}}</button>
                <button class="verificationBtn" v-if="!isVerify">{{yzmContent}}</button>
            </div>
            <div class="phoneCode">
                <img src="./img/phoneCode.png" alt="">
                <input type="text" placeholder="请输入短信验证码" v-model="verifyCode">
            </div>
            <div class="childName">
                <img src="./img/card.png" alt="">
                <input type="text" @change="IdCodeValid(card)" placeholder="请输入身份证号" v-model="card">
            </div>

            <span class="agreeMent"><input type="checkbox" v-model="isAgree"><span @click="goAgree()">同意使用条款和隐私权限</span></span>
            <span class="agreeMent" @click="goCaozuo()">新手操作指引</span>


            <div class="reg">
                <button @click="reg()">注册代理</button>
            </div>
        </div>


    </div>

</template>

<script>
    import request from '../../static/js/request';
    import result from '../../static/js/result';
    import getParam from '../../static/js/getParam';

    export default {
        data() {
            return {
                userName: this.$route.query.name?this.$route.query.name:'',//姓名
                userPhone:this.$route.query.phone?this.$route.query.phone: '',//手机号
                verifyCode: '',//验证码
                isVerify: true,//验证码状态
                yzmContent: '获取验证码',
                card: '',//身份证号
                cardState: false,//身份证号状态
                docmHeight: document.documentElement.clientHeight,  //默认屏幕高度
                showHeight: document.documentElement.clientHeight,   //实时屏幕高度
                topShow: false,
                isAgree: false

            }
        },
        mounted() {
            window.onresize = () => {
                return (() => {
                    this.showHeight = document.body.clientHeight;
                })()
            }
        },
        created() {


        },
        methods: {
            /**
             * 查看操作指引
             * **/
            goCaozuo(){
                this.$router.push('/caoZuo')
            },
            /**
             * 查看服务协议
             * **/
            goAgree() {
                if(this.$route.query.agentId){
                    this.$router.push('/agreeMent?agentId='+this.$route.query.agentId)
                }else{
                    this.$router.push('/agreeMent')
                }

            },
            /*验证身份证号*/
            IdCodeValid(code) {
                console.log(code)
                //身份证号合法性验证
                //支持15位和18位身份证号
                //支持地址编码、出生日期、校验位验证
                var city = {
                    11: "北京",
                    12: "天津",
                    13: "河北",
                    14: "山西",
                    15: "内蒙古",
                    21: "辽宁",
                    22: "吉林",
                    23: "黑龙江 ",
                    31: "上海",
                    32: "江苏",
                    33: "浙江",
                    34: "安徽",
                    35: "福建",
                    36: "江西",
                    37: "山东",
                    41: "河南",
                    42: "湖北 ",
                    43: "湖南",
                    44: "广东",
                    45: "广西",
                    46: "海南",
                    50: "重庆",
                    51: "四川",
                    52: "贵州",
                    53: "云南",
                    54: "西藏 ",
                    61: "陕西",
                    62: "甘肃",
                    63: "青海",
                    64: "宁夏",
                    65: "新疆",
                    71: "台湾",
                    81: "香港",
                    82: "澳门",
                    91: "国外 "
                };
                var row = true;
                var msg = "验证成功";

                if (!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|[xX])$/.test(code)) {
                    row = false,
                        msg = "身份证号格式错误";
                } else if (!city[code.substr(0, 2)]) {
                    row = false,
                        msg = "身份证号地址编码错误";
                } else {
                    //18位身份证需要验证最后一位校验位
                    if (code.length == 18) {
                        code = code.split('');
                        //∑(ai×Wi)(mod 11)
                        //加权因子
                        var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
                        //校验位
                        var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
                        var sum = 0;
                        var ai = 0;
                        var wi = 0;
                        for (var i = 0; i < 17; i++) {
                            ai = code[i];
                            wi = factor[i];
                            sum += ai * wi;
                        }
                        if (parity[sum % 11] != code[17].toUpperCase()) {
                            row = false,
                                msg = "身份证号校验位错误";
                        }
                    }
                }
                console.log(row)
                if (!row) {
                    weui.alert(msg);
                    return false
                } else {
                    this.cardState = true
                }

            },
            userPhoneTest(phone) {
                if (!(/^1[3456789]\d{9}$/.test(phone))) {
                    weui.alert("请输入正确手机号");
                    this.userPhone = '';
                    return false;
                }
            },

            /**
             * 获取手机验证码
             * */
            async yzm() {
                if (!this.userPhone) {
                    weui.alert('手机号不能为空')
                    return false
                }
                let params1 = {
                    userPhone: this.userPhone
                }
                let orderInfo = await request('/api/open/user/orderUserState',
                    'post', params1, 'application/x-www-form-urlencoded')
                // loading.hide()
                console.log(orderInfo)
                let info1 = result(orderInfo)
                if(orderInfo.code==500||orderInfo.data.length==0){
                    weui.alert('只允许在下单支付成功后才可以申请成为代理');
                    return false;
                }else{
                    let params = {
                        str: this.userPhone
                    }
                    let submitInfo = await request('/api/open/sms/sendSms',
                        'post', params, 'application/x-www-form-urlencoded')
                    // loading.hide()
                    let info = result(submitInfo)
                    // loading.hide()
                    if (!info) return;
                    weui.toast('已发送');
                    let time = 60;
                    let timer = setInterval(() => {
                        if (time == 0) {
                            clearInterval(timer);
                            this.isVerify = true;
                            this.yzmContent = "获取验证码";
                            return false;
                        } else if (time > 0) {
                            this.yzmContent = time + '秒后重试';
                            this.isVerify = false;
                            time--
                        }
                    }, 1000)
                    console.log(info)
                }

            },


            /**
             * 注册信息
             * */
            async reg() {

                if (!this.userName) {
                    weui.alert("请输入姓名！");
                    return false;
                }
                if (!this.userPhone) {
                    weui.alert("请输入手机号！");
                    return false;
                }
                if (!this.verifyCode) {
                    weui.alert("请输入验证码！");
                    return false;
                }
                if (!this.card) {
                    weui.alert("请输入身份证号！");
                    return false;
                }
                if (!this.cardState) {
                    weui.alert("请输入正确身份证号！");
                    return false;
                }
                if (!this.isAgree) {
                    weui.alert("请同意使用条款和隐私权限！");
                    return false;
                }

                let params = {
                    agentCard: this.card,
                    agentName: this.userName,
                    agentPhone: this.userPhone,
                    verifyCode: this.verifyCode,
                    agentPid:this.$route.query.agentId?this.$route.query.agentId:''
                }
                /*if(this.$route.query.agentId){
                    params.agentPid = this.$route.query.agentId
                }*/
                let regInfo = await request('/api/open/user/registerUserAgent',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(regInfo)
                if (regInfo.code == 500) {
                    weui.alert(regInfo.msg)
                }
                // loading.hide()
                if (!res) return;
                weui.toast('注册成功')
                console.log('注册成功信息', regInfo)
                this.$router.go(-1);
            },
            goBack() {
                this.$router.go(-1)
            },

        },
        activated(){
            this.userName = this.$route.query.name ? this.$route.query.name : ''
            this.userPhone = this.$route.query.phone ? this.$route.query.phone : ''
        },
        watch: {
            showHeight() {
                if (this.docmHeight > this.showHeight) {
                    this.topShow = true
                } else {
                    this.topShow = false
                }
            }
        }
    }
</script>

